.navbar {
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 2rem;
    position: fixed;
    top: 0;
    background: var(--color-bg);
    width: 100%;
}

.navbar-links {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-links_logo {
    margin-right: 2rem;
}

.navbar-links_logo img {
    width: 60px;
    height: 60px;

}
.navbar-links_container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}



.navbar-links_container p,
.navbar-menu_container p {
    color: var(--color-heading);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    cursor: pointer;
    margin: 0 1rem;
    
    letter-spacing: 0.09rem;
    word-spacing: 0.5rem;
}

.navbar-links_container p:hover {
    color: var(--color-text);
}
.navbar-menu-container button {

    padding: 0.5rem 1rem;
    left: 1153px;
    font-weight: 500;
    font-size: 18px;
    top: 55px;
    color: #FFF;
    background: #FF4820;
    border-radius: 5px;
    border: none;
    line-height: 25px;
    font-family: var(--font-family);
    cursor: pointer;
}

 .navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;

}

.navbar-menu img{
    cursor: pointer;
}

.navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0,2)  ;
}


.gpt3_navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3_navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width:1050px){
    .gpt3_navbar-links_container {
        display: none;
    }
    .gpt3_navbar-menu {
        display: flex;
    }
}

@media screen and (max-width:700px){
    .gpt3_navbar-{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width:550px){
    .navbar{
        padding: 2rem ;
    }
    .navbar-sign {
        display: none;
    }

    .navbar-menu-container {
        top:20px;
    } 
    .navbar-menu_container-links-sign {
        display: block;

       
    }

    .navbar-menu_container-links-sign button{

        padding: 0.5rem 1rem;
        left: 1153px;
        font-weight: 500;
        font-size: 18px;
        top: 55px;
        color: #fff;
        background: #FF4820;
        border-radius: 5px;
        border: none;
        line-height: 25px;
        font-family: var(--font-family);
        cursor: pointer;
    }
    
}

